/* eslint-disable no-await-in-loop */
import React from 'react';
import Form from 'react-bootstrap/lib/Form';
import Row from 'react-bootstrap/lib/Row';
import Col from 'react-bootstrap/lib/Col';
import Button from 'react-bootstrap/lib/Button';
import ProgressBar from 'react-bootstrap/lib/ProgressBar';

const jimp = require('jimp');
const download = require('downloadjs');

let api = {};
// eslint-disable-next-line
Module.onRuntimeInitialized = async () => {
  api = {
    // eslint-disable-next-line
    version: Module.cwrap('version', 'number', []),
    // eslint-disable-next-line
    create_buffer: Module.cwrap('create_buffer', 'number', ['number', 'number']),
    // eslint-disable-next-line
    destroy_buffer: Module.cwrap('destroy_buffer', '', ['number']),
    // eslint-disable-next-line
    encode: Module.cwrap('encode', '', ['number', 'number', 'number', 'number']),
    // eslint-disable-next-line
    free_result: Module.cwrap('free_result', '', ['number']),
    // eslint-disable-next-line
    get_result_pointer: Module.cwrap('get_result_pointer', 'number', []),
    // eslint-disable-next-line
    get_result_size: Module.cwrap('get_result_size', 'number', []),
  };
  console.log(`libwebp loaded, api version: ${api.version()}`);
};

function convertImageDataToWebpURL(imageData, quality) {
  const p = api.create_buffer(imageData.width, imageData.height);
  // eslint-disable-next-line no-undef
  Module.HEAP8.set(imageData.data, p);
  api.encode(p, imageData.width, imageData.height, quality);
  const resultPointer = api.get_result_pointer();
  const resultSize = api.get_result_size();
  // eslint-disable-next-line no-undef
  const resultView = new Uint8Array(Module.HEAP8.buffer, resultPointer, resultSize);
  // eslint-disable-next-line no-undef
  const result = new Uint8Array(resultView);
  api.free_result(resultPointer);
  api.destroy_buffer(p);
  return btoa(String.fromCharCode.apply(null, result));
}

function convertURLToWebpURL(url, quality) {
  // eslint-disable-next-line consistent-return
  return new Promise((resolve, reject) => {
    if (url == null) return reject();
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const image = new Image();
    image.addEventListener('load', () => {
      canvas.width = image.width;
      canvas.height = image.height;
      context.drawImage(image, 0, 0, canvas.width, canvas.height);

      const useChrome = false;
      if (useChrome) {
        resolve(canvas.toDataURL('image/webp', quality));
      } else {
        const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
        resolve(convertImageDataToWebpURL(imageData, Math.ceil(quality * 100)));
      }
    }, false);
    image.src = url;
  });
}

function addTextToURL(URI, text) {
  // eslint-disable-next-line consistent-return
  return new Promise(((resolve, reject) => {
    if (URI == null) return reject();
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const image = new Image();
    image.addEventListener('load', () => {
      canvas.width = image.width;
      canvas.height = image.height;

      context.drawImage(image, 0, 0, canvas.width, canvas.height);

      context.save();

      // draw circle
      context.beginPath();
      context.arc(canvas.width - 0 - 16, canvas.height - 0 - 16, 16, 0, 2 * Math.PI);
      context.fillStyle = 'rgba(53, 67, 90, 0.85)';
      context.fill();

      context.restore();

      // draw text
      context.font = '30px helvetica';
      context.textAlign = 'center';
      context.fillStyle = '#f7f7f7';
      context.lineWidth = 2;
      context.fillText(text, canvas.width - 0 - 16, canvas.height - 0 - 6);
      resolve(canvas.toDataURL('image/png'));
    }, false);
    image.src = URI;
  }));
}


async function resizeAndConvert(input, px, toWebp) {
  return jimp.read(input).then((image) => {
    if (toWebp) {
      return image.contain(px, px).getBase64Async(jimp.MIME_PNG)
        .then(uri => convertURLToWebpURL(uri, 1).then(async (_webpUri) => {
          let webpUri = _webpUri;

          let quality = 1.0;
          while (btoa(webpUri).length > 99999 && quality > 0.2) {
            quality -= 0.08;
            console.error(`WebP size ${Math.ceil(btoa(webpUri).length / 1024)}kb exceeded 100kb, resizing with quality ${quality}`);
            // eslint-disable-next-line no-await-in-loop
            webpUri = await convertURLToWebpURL(uri, quality);
          }
          return webpUri;
        }));
    }
    return image.contain(px, px).getBase64Async(jimp.MIME_PNG);
  });
}

async function loadFile(file, type) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onloadend = async () => {
      try {
        if (type === 'tray') {
          resolve(await resizeAndConvert(reader.result, 96, false));
        } else if (type === 'stickers') {
          resolve(await resizeAndConvert(reader.result, 512, true));
        }
      } catch (e) {
        reject(e);
      }
    };
    reader.readAsArrayBuffer(file);
  });
}



class ImageUpload extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      identifier: '',
      name: '',
      publisher: '',
      trayFile: null,
      stickersFiles: null,
      isSubmitting: false,
      progress: 0,
      errorMsg: '',
    };
  }

  handleSubmit = (e) => {
    e.preventDefault();

    this.setState({
      progress: 0,
      errorMsg: '',
      isSubmitting: true,
    });

    const {
      identifier, name, publisher, trayFile, stickersFiles,
    } = this.state;

    (async () => {
      const tray = await loadFile(trayFile, 'tray');
      const stickersInPack = [];

      const numOfPacks = Math.ceil(stickersFiles.length / 30);
      const trays = [];

      for (let pack = 0; pack < numOfPacks; pack++) {
        stickersInPack.push([]);
        trays.push(numOfPacks === 1 ? tray : await addTextToURL(tray, `${pack + 1}`));
      }

      for (let i = 0; i < stickersFiles.length; i++) {
        const sticker = await loadFile(stickersFiles[i], 'stickers');
        this.setState({ progress: (i + 1) / stickersFiles.length * 100 });
        stickersInPack[Math.floor(i / 30)].push(sticker);
      }

      if (numOfPacks > 1 && stickersInPack[numOfPacks - 1].length < 3) {
        stickersInPack[numOfPacks - 1] = [
          ...stickersInPack[numOfPacks - 2].splice(-(3 - stickersInPack[numOfPacks - 1].length)),
          ...stickersInPack[numOfPacks - 1],
        ];
      }

      stickersInPack.map((pack, index) => ({
        identifier: identifier + (index === 0 ? '' : `_${index + 1}`),
        name: name + (index === 0 ? '' : ` (${index + 1})`),
        publisher,
        tray_image: trays[index].replace('data:image/png;base64,', ''),
        stickers: pack.map(sticker => ({
          image_data: sticker.replace('data:image/webp;base64,', ''),
        })),
      })).forEach((pack) => {
        // download(`data:application/json;base64,${Buffer.from(JSON.stringify(pack)).toString('base64')}`, `${pack.identifier}.json`, 'application/json');
        download(new Blob([JSON.stringify(pack)]), `${pack.identifier}.json`, 'application/json');
        this.setState({ isSubmitting: false });
      });
    })().catch((error) => {
      console.error(error);
      this.setState({
        errorMsg: error.message || e.toString(),
        isSubmitting: false,
      });
    });
  };

  handleImageChange = (e, type) => {
    e.preventDefault();

    if (type === 'tray') {
      this.setState({
        trayFile: e.target.files[0],
      });


      // for debugging photo
      // setTimeout(async() => {
      //   const tray = await loadFile(this.state.trayFile, 'tray');
      //   const tray2 = await addTextToURL(tray, '8');
      //   this.setState({
      //     imagePreviewUrl: tray2,
      //   })
      // }, 500)

      // setTimeout(async () => {
      //   const tray = await loadFile(this.state.trayFile, 'tray');
      //   const tray2 = await addTextToURL(tray, '8');
      //   this.setState({
      //     imagePreviewUrl: tray2,
      //   });
      // }, 500);

    } else if (type === 'stickers') {
      this.setState({
        stickersFiles: e.target.files,
      });
    }
  };

  handleInputChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };

  isFormValid = () => this.isIdentifierValid() && this.state.identifier && this.state.name && this.state.publisher
    && this.state.trayFile != null && this.state.stickersFiles && this.state.stickersFiles.length >= 3;

  isSubmittable = () => this.isFormValid() && !this.state.isSubmitting;

  isIdentifierValid = () => /^[A-Za-z0-9-_.\s]*$/.test(this.state.identifier);

  render() {
    return (
      <Form onSubmit={e => this.handleSubmit(e)}>
        <Form.Group as={Row} controlId="identifier">
          <Form.Label column sm={4}>Identifier</Form.Label>
          <Col sm={6}>
            <Form.Control type="text" required onChange={this.handleInputChange} isInvalid={!this.isIdentifierValid()} />
            <Form.Control.Feedback type="invalid">
              {'Alphanumeric characters with ".", "_", "-" or " " only '}
            </Form.Control.Feedback>
          </Col>
        </Form.Group>

        <Form.Group as={Row} controlId="name">
          <Form.Label column sm={4}>Pack Name</Form.Label>
          <Col sm={6}>
            <Form.Control type="text" required onChange={this.handleInputChange} />
          </Col>
        </Form.Group>

        <Form.Group as={Row} controlId="publisher">
          <Form.Label column sm={4}>Publisher</Form.Label>
          <Col sm={6}>
            <Form.Control type="text" required onChange={this.handleInputChange} />
          </Col>
        </Form.Group>

        <Form.Group as={Row} controlId="formTrayFile">
          <Form.Label column sm={4}>Tray Icon</Form.Label>
          <Col sm={8}>
            <div className="custom-file">
              <Form.Control
                className="custom-file-input"
                type="file"
                required
                isValid={false}
                onChange={e => this.handleImageChange(e, 'tray')}
                accept="image/png"
              />
              <Form.Label className="custom-file-label">{this.state.trayFile ? this.state.trayFile.name : 'Choose file (PNG, any resolution)' }</Form.Label>
            </div>
          </Col>
        </Form.Group>

        <Form.Group as={Row} controlId="formStickersFiles">
          <Form.Label column sm={4}>Stickers (3 or more images)</Form.Label>
          <Col sm={8}>
            <div className="custom-file">
              <Form.Control
                className="custom-file-input"
                type="file"
                required
                multiple
                onChange={e => this.handleImageChange(e, 'stickers')}
                accept="image/png"
              />
              <Form.Label className="custom-file-label">{this.state.stickersFiles && this.state.stickersFiles.length > 0 ? `${this.state.stickersFiles.length} file(s) selected` : 'Choose file (PNG, any resolution)' }</Form.Label>
            </div>
          </Col>
        </Form.Group>
        <Button type="submit" disabled={!this.isSubmittable()}>Convert Images</Button>
        <ProgressBar style={{ transition: 'none', marginTop: '15px' }} now={this.state.progress} hidden={!this.state.isSubmitting && !this.state.errorMsg} variant={this.state.errorMsg ? 'danger' : ''} />
        <p
          className={this.state.errorMsg ? 'text-danger' : 'text-success'}
          hidden={!this.state.errorMsg && this.state.progress !== 100}
        >
          {this.state.errorMsg || 'Images converted!'}
        </p>
        {/* <img src={this.state.imagePreviewUrl || ''} /> */}
      </Form>
    );
  }
}


export default ImageUpload;
